import React, { Component } from 'react';
import Card from '@mui/material/Card';
import Divider from '@mui/material/Divider';
import Medal from './Medal';
import Badge from '@mui/material/Badge';





class Country extends Component {

    totalCountryMedals(country, medals){
        let total = 0;
        medals.forEach(medal => {total += country[medal.name];});
        return total;
    }


   render() { 
    const { country, handleIncrement, handleDecrement, medals } = this.props;
     return ( 
        
        <div style={{ width: '250px',  margin: '0 auto', paddingTop: '30px' }}>
            <Card variant="outlined" style={{ backgroundColor: 'lightPink', width:'250px', boxShadow: '10px 10px 5px grey' }}>
                <div className="Country" style={{ color: 'white' }}>{country.name}  <Badge className="Badge" badgeContent={this.totalCountryMedals(country, medals)} color="secondary"></Badge></div> 
                <Divider variant="middle" />
                { medals.map(medal => 
                <Medal 
                    key={ medal.id } 
                    country={ country }
                    medal={medal}
                    handleIncrement={handleIncrement }
                    handleDecrement={handleDecrement }
                    />
                )}
                
            </Card>
        </div>
        
     
    );
   }
 }

 export default Country
