import React, { Component } from 'react';
import Country from './components/Country';
import './App.css';

class App extends Component {
  state = {
    countries: [
      { id: 1, name: 'United States', gold: 2, silver: 1, bronze: 4 },
      { id: 2, name: 'China', gold: 3, silver: 2, bronze: 0  },
      { id: 3, name: 'Germany', gold: 0, silver: 0, bronze: 2  },
    ],
    medals: [
      {id: 1, name: 'gold'},
      {id: 2, name: 'silver'},
      {id: 3, name: 'bronze'},
    ]
  }

  handleIncrement = (countryID, medal) => {
    const countryIndex = this.state.countries.findIndex((c) => c.id === countryID);
    const countriesMutable = [...this.state.countries];
    countriesMutable[countryIndex][medal] += 1;
    this.setState({countries:countriesMutable});
  }

  handleDecrement = (countryID, medal) => {
    const countryIndex = this.state.countries.findIndex((c) => c.id === countryID);
    const countriesMutable = [...this.state.countries];
      if(countriesMutable[countryIndex][medal] >= 0){
        countriesMutable[countryIndex][medal] -= 1;
      }
    this.setState({countries:countriesMutable});
  }

  getTotalMedalsForAll(){
    const goldMedals = this.state.countries.reduce((a, b) => a + b.gold, 0);
    const silverMedals = this.state.countries.reduce((a, b) => a + b.silver, 0);
    const bronzeMedals = this.state.countries.reduce((a, b) => a + b.bronze, 0);
    return goldMedals + silverMedals + bronzeMedals;
  }

  

  render() { 
    return ( 
      <div className="App">
          <h1 className='Country'>Total Medals: {this.getTotalMedalsForAll()}</h1>
        { this.state.countries.map(country => 
          <Country 
            key={ country.id } 
            country={ country }
            medals={this.state.medals}
            handleIncrement={ this.handleIncrement }
            handleDecrement={ this.handleDecrement }
             />
        )}
      </div>
     );
  }
}
 
export default App;
